<template>
  <div class="products">
    <h1>List of {{ products.length }} Products</h1>
    <b-list-group>
      <product-item v-for="product in products"></product-item>
    </b-list-group>
  </div>
</template>

<script>
import { Api } from '@/Api'
import ProductItem from '@/components/ProductItem'

export default {
  name: 'ListProducts',
  data() {
    return {
      products: []
    }
  },
  mounted() {
    this.getProducts()
  },
  methods: {
    getProducts() {
      Api.get('/products')
        .then(reponse => {
          this.products = reponse.data.products
        })
        .catch(error => {
          this.products = []
          console.log(error)
        })
        .then(() => {
          // This code is always executed (after success or error).
        })
    }
  },
  components: {
    ProductItem
  }
}
</script>

